.card {
  background: #fff;
  border-radius: 2px;
  margin: 1rem;
  position: relative;
  width: 200px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}
.card > .img-container {
  height: 200px;
  text-align: center;
  background: black;
}
.card > .img-container > img {
  background-size: cover;
  height: 100%;
  width: 100%;
}



